import _ from 'lodash';
import { Model } from './base/Model';
export class AppState extends Model {
    constructor() {
        super(...arguments);
        this.order = {
            email: '',
            phone: '',
            address: '',
            payment: null,
            items: [],
            total: 0,
        };
        this.formErrors = {};
    }
    setCatalog(items) {
        this.catalog = items.map((item) => {
            return {
                id: item.id,
                description: item.description,
                image: item.image,
                title: item.title,
                category: item.category,
                price: item.price,
            };
        });
        this.emitChanges('items:changed', { catalog: this.catalog });
    }
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    toggleOrderedItem(id, isIncluded) {
        if (isIncluded) {
            //   
            this.order.items = _.uniq([...this.order.items, id]);
        }
        else {
            //  
            this.order.items = _.without(this.order.items, id);
        }
    }
    clearBasket() {
        this.order.items.forEach((id) => {
            this.toggleOrderedItem(id, false);
        });
    }
    getTotal() {
        return this.order.items.reduce((a, c) => a + this.catalog.find((it) => it.id === c).price, 0);
    }
    set total(total) {
        this.order.total = total;
    }
    //       
    itemCount() {
        return this.order.items.length;
    }
    // 
    getSelectedItems() {
        return this.catalog.filter((item) => this.order.items.includes(item.id));
    }
    setOrderField(field, value) {
        if (field !== 'payment') {
            this.order[field] = value;
        }
        if (this.validateOrder()) {
            this.events.emit('order:ready', this.order);
        }
    }
    setPaymentField(payment) {
        this.order.payment = payment;
    }
    //   
    validateOrder() {
        const errors = {};
        if (this.order.payment === null) {
            errors.payment = '   ';
        }
        if (!this.order.address) {
            errors.address = '  ';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    setContactField(field, value) {
        if (field !== 'payment') {
            this.order[field] = value;
        }
        if (this.validateContacts()) {
            this.events.emit('order:ready', this.order);
        }
    }
    //   
    validateContacts() {
        const errors = {};
        if (!this.order.email) {
            errors.email = '  email';
        }
        if (!this.order.phone) {
            errors.phone = '  ';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:contactsChange', this.formErrors);
        return Object.keys(errors).length === 0;
    }
}
